import { AppBar, Toolbar, styled } from "@mui/material";

import { useContext, useEffect } from "react";
import { DataContext } from "../context/DataProvider";

import ViewArrayIcon from "@mui/icons-material/ViewArray";

const Container = styled(AppBar)`
  background: #060606;
  position: static;
  border-bottom: 1px solid #2f2f2f;
  height: 9vh;
`;

const Tool = styled(Toolbar)`
  display: flex;
  justify-content: space-between;
`;

const ViewIcon = styled(ViewArrayIcon)`
  &:hover {
    transform: scale(1.2);
  }
`;

const Header = () => {
  const { copy, setCopy, view, setView } = useContext(DataContext);

  useEffect(() => {
    const timeout = setTimeout(() => {
      setCopy(false);
    }, 1500);
    return () => clearTimeout(timeout);
  }, [copy]);

  const logo =
    "data:image/png;base64,";
  return (
    <Container>
      <Tool>
        <img
          src={logo}
          alt="logo"
          style={{ width: 40, cursor: `pointer` }}
          onClick={() => window.location.reload()}
        />
        {copy ? (
          <p> Code Copied ! </p>
        ) : (
          <h4>
            <em>Unleash Your Creative Code</em>
          </h4>
        )}

        <ViewIcon
          style={{
            // fontSize: view && `1.9rem`,
            transform: view && `rotate(90deg)`,
            border: view && "1px dotted #fff",
            transition: `all .1s ease-in-out`,
            cursor: `pointer`,
          }}
          onClick={() => setView((prevState) => !prevState)}
        />
      </Tool>
    </Container>
  );
};

export default Header;
